'use strict';
import {EventTarget} from "../common/event";
import {SynchronousJsonPortMarshaler} from "./marshaler";
import {ResourceMap} from "../common/resourceMap";
export class ResourcesMarshaler extends SynchronousJsonPortMarshaler {
    constructor(control: EventTarget, public resourceMap: ResourceMap, public defaultAlias: string, public genericError: any) {
        super(control);
    }
    private getResourceMapEventArgs(): any {
        return {
            ResourceMap: this.resourceMap,
            DefaultAlias: this.defaultAlias,
            GenericError: this.genericError
        };
    }
    public initializeState(): void {
        this.fireEvent('resourcesinitialized', this.getResourceMapEventArgs());
    }
    public fireResourcesChangedEvent(resourceMap: ResourceMap): void {
        this.resourceMap = resourceMap;
        this.fireEvent('resourceschanged', this.getResourceMapEventArgs());
    }

}